const simplifyUrl = (url) => {
 return url.replace('https://','')
 .replace('http://','')
 .replace('www.','')
 .replace(/\/.*/,'')
}//简化网址的函数

const render = () => {
    $siteList.find('li:not( #last)').remove()
    siteListArray.forEach((node,index) => {
        const $li = $(`
            <li>
                <div class="site">
                    <div class="logo">${node.logo}</div>
                    <div class="link">${simplifyUrl(node.url)}</div>
                    <div class='close'>
                        <svg class="icon" aria-hidden="true">
                            <use xlink:href="#icon-Close"></use>
                        </svg>
                    </div>
                </div>
            </li>
        `).insertBefore($lastLi)
        $li.on('click',()=>{
            window.open(node.url)
        })
        $li.on('click','.close',(e) => {
            e.stopPropagation();
            console.log(siteListArray.index)
            siteListArray.splice(index,1)
            render()
        })
        const $icon = $li.find('.icon')
    })
}//渲染siteList的函数

render()

$('.addNew')
    .on('click',()=>{
        let url = window.prompt('请输入需要添加的网址')
        if(url.indexOf('https://')!==0){
            url = 'https://' + url
        }
        siteListArray.push({
            logo:url[8],
            logoType:"text",
            url: url
        })
        render()
    })//新建标签的监听

window.onbeforeunload = () =>{
    const string = JSON.stringify(siteListArray)
    //localStation只支持存储字符串
    localStorage.setItem('siteListArray',string)
}//页面退出存储标签页数据

$(document).on('keypress',(e) => {
    const {key} = e
    for(let i =0; i<siteListArray.length; i++){
        if(siteListArray[i].logo.toLowerCase() === key){
            window.open(siteListArray[i].url)
        }
    }
})